   flex-direction: column;
   overflow-y: scroll;
}
.item-list {
   max-width: 70%;
   flex-wrap: wrap;
   border: 1px solid black;
   padding: 5px;
}
.human {
   align-self: flex-end;
   text-align: right;
}

.bot {
   align-self: flex-start;
}
